  font-family: 'Roboto Condensed-bestchildren';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff") format("woff");
}

/* roboto-condensed-700 - cyrillic_latin*/
@font-face {
  font-family: 'Roboto Condensed-bestchildren';
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff") format("woff");
}

