@font-face {
  font-family: 'Oswald-bestchildren';
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/oswald-v29-cyrillic_latin-regular.woff2") format("woff2"), url("../fonts/oswald-v29-cyrillic_latin-regular.woff") format("woff");

  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

